<template>
  <v-container>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <div v-if="!overlay">
    <v-carousel
      height="auto"
      hide-delimiters
      show-arrows-on-hover
      >
      <v-carousel-item
        v-for="mainEvent in getRecentEvents"
        :key="mainEvent.id"
        :src="'/images/' + mainEvent.image"
        cycle
        @click="$router.push({ path: `/event/${mainEvent.id}` })"
      >
      </v-carousel-item>
    </v-carousel>
  <v-row style="margin-top: 20px;">
    <v-col 
      :lg="12 / getRecentEvents[0].events.length" 
      sm="12" 
      md="6" 
      v-for="(event,id) in getRecentEvents[0].events" 
      :key="id"
    >
      <v-card
      style="margin-bottom: 20px;"
      > 
      <router-link :to="'/event/' + event.main_id">
        <v-img
          class="white--text align-end"
          height="200px"
          :src="image[id]"
        >
        </v-img>
      </router-link>
        <v-card-text>
          <p style="font-size:1rem !important;">
          {{ event.title }}</p>
          <v-progress-linear
          color="green"
          height="30"
          style="margin-bottom: 20px;"
          :value="getNow"
          v-if="event.isActive == 1"
        ><template>
          <div class="timeInfo">
             {{ new Date(event.timeStart).toLocaleTimeString() }}
             -
             {{ new Date(event.timeStop).toLocaleTimeString() }}
           </div>
        </template>
        </v-progress-linear>
        <!-- <div v-else> -->
          <div v-else class="timeInfo"><v-icon>mdi-clock</v-icon>
             {{ new Date(event.timeStart).toLocaleTimeString() }}
             -
             {{ new Date(event.timeStop).toLocaleTimeString() }}
           </div>
        <!-- </div> -->
        <p style="margin-top: 10px;">
          <v-icon size="20">mdi-google-maps</v-icon>{{ event.place }}</p>
        </v-card-text>

        <v-expansion-panels
        multiple
        flat
        style="margin-top: -45px;"
      >
      <v-expansion-panel v-for="subev in event.subevents" :key="subev.id">

        <v-expansion-panel-content>
          <p>Время проведения:</p>
          {{ new Date(subev.subTimeStart).toLocaleTimeString() }} -
          {{ new Date(subev.subTimeStop).toLocaleTimeString() }}
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
          <router-link :to="'/event/' + event.main_id">
            <v-container class="text-center" align="center"
              style="margin-top:20px; padding: 5px; width: 120px; font-size: 0.726rem"
            >
            ПОДРОБНЕЕ
            </v-container>
        </router-link>
        <!-- </v-card-actions> -->
        </v-card>
    </v-col>
  </v-row>
</div>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
  export default {
    data () {
      return {
        image: [
          '/images/era.jpg',
          '/images/2.jpg',
          '/images/3.jpg',
        ],
        overlay: true
      }
    },
    async mounted() {

      if (!localStorage.getItem('user') || Object.keys(this.getUser).length == 0) {
        this.$fire({
            title: "Уведомление",
            text: "Необходимо авторизоваться!",
            type: "error",
        })
        .then(this.$router.push('/howtouse'));
      }
      document.title = 'Технополис "ЭРА" | Главная';
      this.$emit('update:title', 'Главная')

      await this.$store.dispatch('getRecentEvents')
      this.overlay = false

      await this.getBlue()
      await this.updateBarFunc()
      setInterval(this.updateBarFunc, 2000)

    },

    computed: {
       ...mapGetters(['getRecentEvents', 'getUser', 'getNow']),
    },

    methods: {
      ...mapActions(['updateBar']),
      timeBar(start, stop) {
        var _start = new Date(start).getTime()
        var _stop = new Date(stop).getTime()
        var now = Date.now()
        return (((now - _start) / (_stop - _start)) * 100)
      },

      updateBarFunc() {
        this.getBlue()
          this.updateBar([this.bar_date.timeStart, this.bar_date.timeStop])
          this.value = this.getNow
          this.getRecentEvents
      },

      getBlue() {
                this.getRecentEvents[0].events.forEach(d => {
                    if (new Date(d.timeStart) < Date.now() && new Date(d.timeStop) > Date.now()) {
                        d.isActive = 1
                        this.bar_date = d
                    }
                    else if (new Date(d.timeStop) < Date.now()) {
                        d.isActive = 2;
                    }
                    else if (new Date(d.timeStart) > Date.now()) {
                        d.isActive = 0;
                    }
                })
            },
    }
  }
</script>

